<!--  -->
<template>
    <div class='search'>
        <div class='searchBox'>
            <input type="text" placeholder="请输入搜索内容"  class='inp' v-model="searchValue"   >
            <span class='searchBtn' @click="search" v-if='searchBtn' >搜索</span>
            <span class='searchBtn' @click="search" v-else>取消</span>
        </div>
        <div class='history' v-if='searchBtn' >
            <div class='history-title'>
                <span class='history-text'>搜索历史</span>
                <img src="../../../static/image/del.png" alt=""  class='del-icon' @click='delName' >
            </div>
            <div class='history-list' v-if='searchList.length!=0' >
                <span class='lists' v-for='(item,index) in searchList' :key='index' @click='searchName(item)' >{{item}}</span>
            </div>
            <div class='empty'  v-else>暂无历史记录</div>
        </div>
        <div class='listBox' v-else>
            <div class='list' v-for='(item,index) in list'  :key='index' @click='detailsName(item.productId)' >
                <img :src="item.picture" alt="" class='list-img'>
                <p class='list-name'>{{item.title}}</p>
                <div class='list-money'>
                    <span class='money1'>￥{{item.sellPrice}}</span>
                    <span class='money2' v-if='item.marketPrice!=null' >￥{{item.marketPrice}}</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapActions,mapGetters } from 'vuex'
    import {search} from '../../api/api'
    export default {
    components: {},
    data() {
        return {
            searchValue:'',
            searchBtn:true,
            list:[]   //搜索内容
        };
    },
     //监听属性 类似于data概念
    computed: {
         ...mapGetters(['searchList','allMoney','allSelsect'])
    },
    //方法集合
    methods: {
        ...mapActions(['headTitle','showBtm','goBack','addSearch','delSearch']),
        // 搜索内容
        search:function(){
            this.searchBtn=!this.searchBtn
            if(!this.searchBtn){
                if(this.searchValue==''){
                    this.$dialog({
                        content: "请输入搜索内容",
                        noOkBtn:true,
                        cancelBtnTxt:'我知道了'
                    });
                    this.searchBtn=!this.searchBtn
                }else{
                    this.addSearch(this.searchValue)
                    this.searchName(this.searchValue)
                }
            }else{
                this.searchValue=''
            }
        },
        // 搜索记录
        searchName:function(text){
            this.loading = this.$toast.loading();
            search(text).then(response => {
                this.loading.hide();
                var that=this;
                if(response.data.status==1000){
                    this.$dialog({
                        content: "目前暂无该商品",
                        noOkBtn:true,
                        cancelBtnTxt:'我知道了',
                        onCancelBtn(event) {
                            that.searchBtn=true
                        },
                    });
                }else{
                    this.searchBtn=false
                    this.list=response.data.data
                }
            });
        },
        // 删除搜索记录
        delName:function(){
            var that=this;
            this.$dialog({
                content: "确定删除搜索记录吗？",
                onOkBtn(event) {  //确定按钮点击事件
                    that.delSearch()
                    this.close(); //关闭对话框
                },
            });
            
        },
        // 商品详情
        detailsName:function(id){
            this.$router.push('/shopDetails/'+id)
        }
    },
    created() {
        this.headTitle('搜索')
        this.showBtm(false)
        this.goBack(true)
    },
    mounted() {

    },
    beforeCreate() {}, //生命周期 - 创建之前
    beforeMount() {}, //生命周期 - 挂载之前
    beforeUpdate() {}, //生命周期 - 更新之前
    updated() {}, //生命周期 - 更新之后
    beforeDestroy() {}, //生命周期 - 销毁之前
    destroyed() {}, //生命周期 - 销毁完成
    activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
    }
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
    .search{
        margin-top:90px;
    }
    .searchBox{
        background:#fff;
        width:100%;
        height:100px;
        box-shadow: 0px 10px 14px 0px rgba(0,0,0,0.05);
        padding:0 3%;
        display:flex;
        justify-content:center;
        align-items:center;
    }
    .inp{
        display:block;
        width:84%;
        padding-left:4%;
        height:70px;
        border-radius:50px;
        background:#f5f6f7;
        outline: none;
        border:none;
    }
    .searchBtn{
        width:120px;
        height:70px;
        display: block;
        text-align:center;
        line-height:70px;
        font-size:26px;
        color:#000;
    }
    .history{
        width:92%;
        margin:0 auto;
    }
    .history-title{
        width:100%;
        display:flex;
        justify-content:space-between;
        align-items:center;
        margin-bottom:30px;
        margin-top:50px;
    }
    .history-text{
        font-size:28px;
        font-weight:bold;
    }
    .del-icon{
        width:30px;
        height:30px;
        display:block;
    }
    .history-list{
        width:100%;
        display:flex;
        flex-wrap:wrap;
        align-items:center;
    }
    .lists{
        padding:0 30px;
        height:56px;
        line-height:56px;
        font-size:20px;
        background:#fff;
        border-radius:30px;
        margin-right:30px;
        margin-bottom:30px;
    }
    .empty{
        width:100%;
        height:300px;
        text-align:center;
        line-height:300px;
        font-size:28px;
    }
    .listBox{
        width:95%;
        margin:0 auto;
        display:flex;
        justify-content:space-between;
        flex-wrap: wrap;
        margin-top:30px;
    }
    .list{
        width:347px;
        height:517px;
        background:#fff;
        border-radius:10px;
        margin-bottom:16px;
        overflow: hidden;
    }
    .list-img{
        width:347px;
        height:347px;
        display:block;
        margin-bottom:16px;
    }
    .list-name{
        width:100%;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        font-size:26px;
        color:#000;
        font-weight:bold;
        height:60px;
        line-height:60px;
        margin-bottom:18px;
        padding-left:24px;
    }
    .list-money{
        display:flex;
        align-items:center;
        padding-left:24px;
    }
    .money1{
        font-size:36px;
        color:rgb(255,0,0);
        margin-right:20rpx;
    }
    .money2{
        font-size:24px;
        color:#ccc;
    }
</style>


